"use client";

import { useRouter } from "next/navigation";
import {
  FaPencilAlt,
  FaUser,
  FaCalendarAlt,
  FaEnvelope,
  FaBuilding,
  FaCog,
  FaStore,
  FaMotorcycle,
  FaUserCog
} from "react-icons/fa";

const menuItems = [
  { label: "Bookings", path: "/bookings" },
  { label: "Rental Companies",path: "/rental-companies" },
  { label: "Ebike Management",  path: "/ebikeManagement" },
  { label: "Users Management",  path: "/users" },
  { label: "Customers Management",  path: "/customers" },
  { label: "Battery Management",  path: "/battery" },
  { label: "Subscription Packages",  path: "/subscriptionPackages" },
];

export default function UserTopMenu() {
  const router = useRouter();

  return (
    <div className="hidden md:flex font-inter bg-[#00d289] text-white w-full px-1 py-2 justify-center gap-x-8 items-center">
      {menuItems.map((item) => (
        <button
          key={item.label}
          onClick={() => router.push(item.path)}
          className="flex items-center space-x-1 font-medium text-sm"
        >
          <span>{item.label}</span>
        </button>
      ))}
    </div>
  );
}
