'use client';

import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/src/components/ui/dialog';
import { Button } from '@/src/components/ui/button';
import { CheckCircle, AlertCircle, Info, Trash2 } from 'lucide-react';

// ✅ Export riêng NotificationType để các file khác import được
export type NotificationType = 'success' | 'error' | 'info' | 'confirm';

export interface NotificationDialogProps {
  open: boolean;
  type: NotificationType;
  title: string;
  description?: string;
  onClose: () => void;
  onConfirm?: () => void;
}

export default function NotificationDialog({
  open,
  type,
  title,
  description,
  onClose,
  onConfirm,
}: NotificationDialogProps) {
  const renderIcon = () => {
    switch (type) {
      case 'success':
        return <CheckCircle className="text-green-500 w-6 h-6" />;
      case 'error':
        return <AlertCircle className="text-red-500 w-6 h-6" />;
      case 'info':
        return <Info className="text-blue-500 w-6 h-6" />;
      case 'confirm':
        return <Trash2 className="text-orange-500 w-6 h-6" />;
      default:
        return null;
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-md">
        <DialogHeader className="flex items-center gap-3">
          {renderIcon()}
          <DialogTitle>{title}</DialogTitle>
        </DialogHeader>

        {description && <p className="text-sm text-gray-600 mt-2">{description}</p>}

        <DialogFooter className="mt-4">
          {type === 'confirm' ? (
            <>
              <Button variant="ghost" onClick={onClose}>
                Cancel
              </Button>
              <Button variant="destructive" onClick={onConfirm}>
                Confirm
              </Button>
            </>
          ) : (
            <Button onClick={onClose}>OK</Button>
          )}
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
