'use client';

import { useEffect, useState } from "react";
import { db } from "@/src/firebaseConfig";
import { collection, getDocs, query, where, updateDoc, doc } from "firebase/firestore";
import { VehicleIssue, ExtendedVehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";

export function useVehicleIssues(companyId: string, userId: string, role: string) {
  const [issues, setIssues] = useState<ExtendedVehicleIssue[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchIssues = async () => {
      setLoading(true);
      try {
        let q;

        if (role === "Admin" || role === "admin") {
          q = query(collection(db, "vehicleIssues"));
        } else if (["company_owner", "staff", "station_manager", "support"].includes(role)) {
          if (!companyId) {
            setIssues([]);
            setLoading(false);
            return;
          }
          q = query(collection(db, "vehicleIssues"), where("companyId", "==", companyId));
        } else if (role === "technician") {
          if (!userId) {
            setIssues([]);
            setLoading(false);
            return;
          }
          q = query(collection(db, "vehicleIssues"), where("assignedTo", "==", userId));
        } else {
          setIssues([]);
          setLoading(false);
          return;
        }

        const snap = await getDocs(q);

        const ebikeSnap = await getDocs(collection(db, "ebikes"));
        const ebikeMap = new Map<string, any>();
        ebikeSnap.docs.forEach(doc => ebikeMap.set(doc.id, doc.data()));

        const staffSnap = await getDocs(collection(db, "staffs"));
        const staffMap = new Map<string, string>();
        staffSnap.docs.forEach(doc => {
          const staff = doc.data();
          staffMap.set(staff.userId, staff.name || "(Unnamed)");
        });

        const enrichedIssues: ExtendedVehicleIssue[] = snap.docs.map(docSnap => {
          const data = docSnap.data();
          const ebike = ebikeMap.get(data.ebikeId);
          const assignedToName = data.assignedTo ? (staffMap.get(data.assignedTo) || data.assignedTo) : "-";

          return {
            id: docSnap.id,
            companyId: data.companyId,
            stationId: data.stationId,
            ebikeId: data.ebikeId,
            issueType: data.issueType,
            description: data.description,
            photos: data.photos || [],
            status: data.status,
            reportedBy: data.reportedBy,
            assignedTo: data.assignedTo,
            reportedAt: data.reportedAt,
            updatedAt: data.updatedAt,
            companyName: data.companyName || "",
            stationName: data.stationName || "",
            vin: ebike?.vehicleID || "-",
            plateNumber: ebike?.plateNumber || "-",
            assignedToName,
          };
        });

        setIssues(enrichedIssues);
      } catch (err) {
        console.error("❌ Error fetching vehicle issues:", err);
        setIssues([]);
      } finally {
        setLoading(false);
      }
    };

    fetchIssues();
  }, [companyId, userId, role]);

  const updateIssue = async (id: string, data: Partial<VehicleIssue>) => {
    const ref = doc(db, "vehicleIssues", id);
    await updateDoc(ref, data);
  };

  return { issues, loading, updateIssue };
}
