'use client';

import { useState } from "react";
import { useUser } from "@/src/context/AuthContext";
import { useCompanyAndStation } from "@/src/hooks/useCompanyAndStation";
import { useVehicleIssues } from "@/src/hooks/useVehicleIssues";
import { VehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";

import Header from "@/src/components/landingpage/Header";
import Footer from "@/src/components/landingpage/Footer";
import UserTopMenu from "@/src/components/landingpage/UserTopMenu";
import VehicleIssueTable from "@/src/components/vehicleIssues/VehicleIssueTable";
import VehicleIssueUpdateForm from "@/src/components/vehicleIssues/VehicleIssueUpdateForm";
import AssignTechnicianForm from "@/src/components/vehicleIssues/AssignTechnicianForm";
import NotificationDialog from "@/src/components/ui/NotificationDialog";

export default function VehicleIssuesPage() {
  const { user, role } = useUser();
  const normalizedRole = (role || '').toLowerCase();

  const { companyId, companyName, stationId, stationName, loading: companyLoading } = useCompanyAndStation();
  const { issues, updateIssue } = useVehicleIssues(companyId ?? '', user?.uid ?? '', normalizedRole);

  const [selectedIssue, setSelectedIssue] = useState<VehicleIssue | null>(null);
  const [assigning, setAssigning] = useState<VehicleIssue | null>(null);
  const [notification, setNotification] = useState<string | null>(null);

  // Đang loading
  if (!user || !role || companyLoading) {
    return <div className="text-center py-10">Loading...</div>;
  }

  // Nếu cần companyId nhưng chưa có → báo lỗi
  const roleNeedsCompany = !["technician", "admin"].includes(normalizedRole);
  if (!companyId && roleNeedsCompany) {
    return <div className="text-center py-10">Missing company information</div>;
  }

  return (
    <div className="flex flex-col min-h-screen bg-gray-100">
      <Header />
      <UserTopMenu />

      <main className="flex-1 p-6 space-y-8">
        <div className="text-sm text-gray-500 text-right space-y-1">
          {companyId && <div><strong>Company:</strong> {companyName}</div>}
          {stationId && <div><strong>Station:</strong> {stationName}</div>}
          <div><strong>Role:</strong> {normalizedRole}</div>
        </div>

        <h1 className="text-3xl font-bold text-center text-gray-800">🚨 Vehicle Issues Management</h1>

        <div className="bg-white border rounded-xl shadow p-6 space-y-6">
          <VehicleIssueTable
            issues={issues}
            onEdit={(issue) => {
              if (normalizedRole === "company_owner") {
                setAssigning(issue);
              } else {
                setSelectedIssue(issue);
              }
            }}
          />
        </div>

        {selectedIssue && (
          <div className="bg-white border rounded-xl shadow p-6 space-y-4">
            <h2 className="text-lg font-semibold">Update Issue</h2>

          </div>
        )}

        {assigning && normalizedRole === "company_owner" && (
          <div className="bg-white border rounded-xl shadow p-6 space-y-4">
            <h2 className="text-lg font-semibold">Assign Technician</h2>
            <AssignTechnicianForm
              companyId={companyId ?? ''}
              onAssign={async (userId) => {
                await updateIssue(assigning.id, { assignedTo: userId, status: "in_progress" });
                setAssigning(null);
                setNotification("Technician assigned successfully!");
              }}
            />
          </div>
        )}
      </main>

      <Footer />

      <NotificationDialog
        open={!!notification}
        type="success"
        title="Success"
        description={notification || undefined}
        onClose={() => setNotification(null)}
      />
    </div>
  );
}
